app.controller('BitbucketController', ['$scope', '$http', '$q', '$window', 'loggedInUser',
    'flightRecorder', 'pivottableService',
    function ($scope, $http, $q, $window, loggedInUser, flightRecorder, pivottableService) {

        var AP = window.RA || window.AP;

        init();

        function init() {
            AP.resize();

            $scope.replyTo = loggedInUser.displayName + ' <' + loggedInUser.emailAddress + '>';

            var closeDialogFunction = function() {
                $scope.$apply(function() {
                    $scope.loading = true;
                });
                var $blob = $scope.includeSupportData.enabled ? flightRecorder.zipBlob() : $q.resolve();
                $blob.then(function (blob) {
                    var fd = new FormData();
                    if (blob) {
                      var file = new File([blob], "supportData.zi_");
                      fd.append('supportData', file);
                    }
                    fd.append('replyTo', $scope.replyTo);
                    fd.append('title', $scope.title);
                    fd.append('content', $scope.content || '');
                    $http.post($window.mailerUrl, fd, {transformRequest: angular.identity, headers: {'Content-Type': undefined}}).then(function(created) {
                        AP.Dialog.close('submit');
                    })
                });
            };

            var paramsDeferred = $q.defer(), $fields, $restricted;
            AP.dialog.getCustomData(function(params) {
                $scope.title = params.errorMessage;
                if (params.stackTrace) {
                    var content = ['', '', '---- Error Stack Trace ----'];
                    content.push.apply(content, params.stackTrace);
                    $scope.content = content.join('\n');
                }
                if (params.options) {
                    $scope.includeSupportData = {enabled: false, options: params.options};
                    flightRecorder.setEnabled(true);
                    flightRecorder.timesheetParams = params.timesheetParams; // redundant?
                    if (Object.keys(params.timesheetParams).length == 0) {
                        if (angular.isFunction(AP.cookie.read)) {
                            var COOKIE_NAME_LOCATION_SEARCH = 'timereports-location-search'; // FIXME: + COOKIE_NAME_SUFFIX
                            AP.cookie.read(COOKIE_NAME_LOCATION_SEARCH, function (data) {
                                flightRecorder.cookie(data);
                                paramsDeferred.resolve(TimesheetUtils.getJson(data));
                            });
                        } else {
                            paramsDeferred.resolve({});
                        }
                    } else {
                        paramsDeferred.resolve(params.timesheetParams);
                    }
                    $fields = pivottableService.getFields();
                    $restricted = pivottableService.restrictedByAuditorsGroups(loggedInUser);
                }
                $scope.$apply();
            });

            AP.events.on('dialog.submit', closeDialogFunction);
            AP.dialog.disableCloseOnSubmit();

            $scope.$watch("includeSupportData.enabled", function (value) {
                if (value) {
                    var submitButton = AP.dialog.getButton('submit');
                    submitButton.disable();
                    $q.all([paramsDeferred.promise, $fields, $restricted]).then(function (values) {
                        var routeParams = values[0], restricted = values[2];
                        if (routeParams.viewId) {
                            viewService.getViews(routeParams.viewId, loggedInUser).then(paramsDeferred.resolve);
                        }
                        var $pivotTable = $scope.includeSupportData.options.restrictable && restricted ? null :
                            pivottableService.getPivotTable(loggedInUser, $scope.includeSupportData.options);
                        $q.when($pivotTable).then(function() {
                            if ($scope.SubmitIssueForm.$valid) {
                                submitButton.enable();
                            }
                        });
                    });
                }
            });

        }
    }
]);
